.skillBlock{
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: coral;
    border-bottom: 2px solid black;
    border-left: 2px solid black;
    border-right: 2px solid black;
}
.skill{
    width: 50px;
    height: 50px;
    background-color: grey;
    margin-bottom: 30px;
}
.skillDescription{
    font-size: 20px;
    font-weight: bolder;
    margin-top: 60px;
    padding: 10px;
    margin-bottom: 30px;
}