.skillsBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #379296;
    padding: 100px 0px;
}

.skillsInner {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 2px solid black;
}

.title {
    background-color: grey;
    padding: 10px;
    margin-bottom: 50px;
}

.skills {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-top: 2px solid black;
}