.mainDiv{
    /*height: 40vh;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #379296;
    padding: 100px 0;
}
.info{
    width: 80%;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 2px solid black;
}
.name{
    background-color: grey;
    padding: 10px;
}
.divCompany{
    width: 100%;
    max-width: 400px;
    min-height: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border: 2px solid black;
}
.company{
    width: 50px;
    height: 50px;
    background-color: grey;
}
.footerInfo{
    background-color: grey;
    padding: 10px;
}